.modal{position:fixed;width:100%;height:100%;left:0;top:0;background:rgba(0,0,0,0.1);display:flex;align-items:center;justify-content:center}.modal .modal-container{box-shadow:1px 1px 10px rgba(0,0,0,0.5);border:1px solid #2b7a78;border-radius:2px}.modal .modal-container .modal-header{height:32px;background:#2b7a78;position:relative}.modal .modal-container .modal-header .modal-title{margin:0;font-size:14px;line-height:32px;padding-left:10px}.modal .modal-container .modal-header .modal-close{position:absolute;top:6px;right:8px;font-size:12px;color:#17252a}.modal .modal-container .modal-content{padding:15px;height:calc(100% - 64px)}.modal .modal-container .modal-footer{height:32px;text-align:right;padding:0 15px}.modal .modal-container .modal-footer button{margin-left:15px}
