*{color:#def2f1;user-select:none;outline:none !important}body{background:#17252a}#app{height:100%}.iconfont{font-size:inherit;color:inherit}.pending{pointer-events:none;position:relative}.pending:after{position:absolute;left:0;top:0;width:100%;height:100%;background:rgba(255,255,255,0.2)}.infinity-hover{transition:filter .3s}.infinity-hover:hover{filter:brightness(1.2)}.infinity-focus{transition:filter .3s}.infinity-focus:focus{filter:brightness(1.2)}input.infinity-err{border-color:#d4594b;transition:border-color .3s}
